import React from 'react';
import { Container, Col, Row, Media } from 'react-bootstrap';
import webdev from '../img/s1.png';
import mobdev from '../img/s2.png';
import embdev from '../img/s3.png';
import mldev from '../img/s4.png';
import GitterCard from './gitter';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faUtensils, faCompactDisc } from '@fortawesome/free-solid-svg-icons';
import 'bootstrap/dist/css/bootstrap.min.css';


class Interests extends React.Component{
    render(){
        return(
            <Container style={{textAlign:'center', paddingTop:'50px', paddingBottom:'50px'}} id={this.props.id}>
                <h1>My Interests</h1>
                <small>Not for show-off !! üòÅ</small>
                <Row style={{paddingTop:'50px', textAlign:'left'}}>
                    <Col xs={12} md={6}>
                        <Media>
                            <img width={64} height={64} className="mr-3" src={webdev} alt=""/>
                            <Media.Body>
                                <h3>Web Designing and UI Development</h3>
                                <p>
                                I like designing websites. This is a a very productive hobby as i have learnt the 
                                different ways and elements required for making a website look beautiful. 
                                This website was also developed by Me and is open-sourced at :{' '}
                                <a href="github.com/pranavms13/pranavms13.github.io">pranavms13/pranavms13.github.io</a>
                                </p>
                            </Media.Body>
                        </Media>
                    </Col>
                    <Col xs={12} md={6}>
                        <Media>
                            <img width={64} height={64} className="mr-3" src={mobdev} alt=""/>
                            <Media.Body>
                                <h3>Mobile App Design and Development</h3>
                                <p>
                                Intrested in developing Android/iOS apps. I have previous experience with Frameworks 
                                like Ionic and Xamarin that i have used to develop a Cryptocurrency Wallet with the{' '}
                                <a href="https://superior-coin.com/">SuperiorCoin</a> Team. The link to the App is{' '}
                                <a href="https://play.google.com/store/apps/details?id=io.supcoin.wallet">HERE</a>
                                </p>
                            </Media.Body>
                        </Media>
                    </Col>
                </Row>
            {/*------------------------------------------------------------------------------------------------------------*/}
                <Row style={{paddingTop:'50px', textAlign:'left'}}>
                    <Col xs={12} md={6}>
                        <Media>
                            <img width={64} height={64} className="mr-3" src={embdev} alt=""/>
                            <Media.Body>
                                <h3>Embedded C and C++ / IoT Development</h3>
                                <p>
                                Extensively used C and C++ in the Development of Cryptocurrency "ReCoal". 
                                Also, i have created and implemented many IoT Devices like Raspberry Pi's, 
                                NodeMCU's, Arduino, etc. for projects like Smart Home. A Small Project for 
                                Controlling Lights at home from any Phone Assistant using a NodeMCU and
                                MQTT is open-sourced at Github : <a href="https://github.com/pranavms13/lights-automation-nodemcu">lights-automation-nodemcu</a>
                                </p>
                            </Media.Body>
                        </Media>
                    </Col>
                    <Col xs={12} md={6}>
                        <Media>
                            <img width={64} height={64} className="mr-3" src={mldev} alt=""/>
                            <Media.Body>
                                <h3>ML and AI Development</h3>
                                <p style={{paddingTop:'15px'}}>
                                Involved in development of 3 Simple projects.</p>
                                <ul>
                                    <li><a href="https://github.com/pranavms13/Credit-Card-Fraud-Detection">Credit Card Fraud Detection using Machine Learning</a></li>
                                    <li><a href="https://github.com/pranavms13/Basic-Sentiment-Analysis">Basic Sentiment Analysis</a></li>
                                    <li>Chat Intent Identification using ML and SpacyNLP</li>
                                </ul>
                            </Media.Body>
                        </Media>
                    </Col>
                </Row>
                {/*------------------------------------------------------------------------------------------------------------*/}
                <Row style={{paddingTop:'50px', textAlign:'left'}}>
                    <Col xs={12} md={6}>
                        <Media>
                            <FontAwesomeIcon icon={faUtensils} size="3x" style={{margin:'18px'}}/>
                            <FontAwesomeIcon icon={faCompactDisc} size="3x" style={{margin:'18px'}}/>
                            <Media.Body>
                                <h4>As a hobby, I love Cooking and DJ'ing</h4>
                            </Media.Body>
                        </Media>
                    </Col>
                    <Col xs={12} md={6}>
                        <GitterCard username="pranavms13" bgc={this.props.bgc}/>
                    </Col>
                </Row>
            </Container>
        )
    }
}
export default Interests;