import React from 'react';
import { Container, Col, Row } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faFacebook, faGithub, faInstagram, faLinkedin, faHackerrank } from '@fortawesome/free-brands-svg-icons';

class PFooter extends React.Component {
    render(){
        return(
            <Container fluid style={{backgroundColor: '#222222', color: '#ffffff',paddingTop:'50px', paddingBottom:'20px', textAlign:'center'}} id={this.props.id}>
                <Row>
                    <Col xs={12}>
                        <h1 style={{color:'#ffffff'}}>Follow me</h1>
                        <small>Let us be social</small>
                        <Container style={{paddingBottom:'20px'}}>
                            <a href="https://github.com/pranavms13" style={{color: '#ffffff'}}><FontAwesomeIcon icon={faGithub} size="2x" style={{margin:'18px'}}/></a>
                            <a href="https://linkedin.com/in/pranavms13" style={{color: '#ffffff'}}><FontAwesomeIcon icon={faLinkedin} size="2x" style={{margin:'18px'}}/></a>
                            <a href="https://www.hackerrank.com/pranavms13" style={{color: '#ffffff'}}><FontAwesomeIcon icon={faHackerrank} size="2x" style={{margin:'18px'}}/></a>
                            <a href="https://instagram.com/pranavms13" style={{color: '#ffffff'}}><FontAwesomeIcon icon={faInstagram} size="2x" style={{margin:'18px'}}/></a>
                            <a href="https://www.facebook.com/pra.nav.718" style={{color: '#ffffff'}}><FontAwesomeIcon icon={faFacebook} size="2x" style={{margin:'18px'}}/></a>
                            
                            
                        </Container>
                        <footer><small>Copyright &copy; {new Date().getFullYear()} Pranav M S. All Rights Reserved</small></footer>
                    </Col>
                </Row>
            </Container>
        );
    }
}
export default PFooter;