import React from 'react';
import { Container, Button, Form } from 'react-bootstrap';
import { message } from 'antd';
import HCaptcha  from '@hcaptcha/react-hcaptcha';
import LoadingOverlay from 'react-loading-overlay';

import 'antd/dist/antd.css';

class DropALine extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            sending:false,
            token:false
        }
        this.onVerifyCaptcha = this.onVerifyCaptcha.bind(this);
        this.sendmsg = this.sendmsg.bind(this);
    }
    onVerifyCaptcha(token) {
        this.setState({token:token})
    }
    sendmsg(e){
        this.setState({sending:true});
        e.preventDefault();
        var name = e.target.elements.formBasicName.value;
        var email = e.target.elements.formBasicEmail.value;
        var subject = e.target.elements.formBasicSubject.value;
        var msg = e.target.elements.formBasicMessage.value;
        if(name && email && subject && msg){
            if(this.state.token){
                fetch('https://pranavmsml-bot.pranavms13.now.sh',{
                    method:'POST',
                    headers: {
                        'Content-Type': 'application/json',
                    },
                    body: JSON.stringify({
                        name:name,
                        email:email,
                        subject:subject,
                        message:msg
                    })
                }).then(response => response.json())
                .then((result) => {
                    if(result.m==="success"){
                        message.success("Message Sent !!", 3, () => {
                            this.setState({sending:false})
                            window.location.reload()
                        })
                    }else{
                        message.error("Message Not Sent !!", 3, () => {
                            this.setState({sending:false})
                        })
                    }
                })
            }else{
                message.error("Please Complete the Captcha !!",3, () => {
                    this.setState({sending:false})
                })
            }
        }else{
            message.error("Please fill all the Details !!",3, () => {
                this.setState({sending:false})
            })
        }
    }
    render(){
        return(
            <LoadingOverlay active={this.state.sending} spinner text='Sending Message'>
            <Container fluid style={{paddingTop:'50px', paddingBottom:'50px', textAlign:'center'}} id={this.props.id}>
                <h1>Drop a Line</h1>
                <small>I'll Get back ASAP..</small>
                <Container style={{paddingTop:'20px', textAlign:'left'}}>
                    <Form onSubmit={this.sendmsg}>
                        <Form.Group controlId="formBasicName">
                            <Form.Label>Enter your Name</Form.Label>
                            <Form.Control type="text" placeholder="Enter Name" />
                        </Form.Group>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Label>Enter your email</Form.Label>
                            <Form.Control type="email" placeholder="Enter Email" />
                        </Form.Group>
                        <Form.Group controlId="formBasicSubject">
                            <Form.Label>Enter Subject</Form.Label>
                            <Form.Control type="text" placeholder="Enter Subject" />
                        </Form.Group>
                        <Form.Group controlId="formBasicMessage">
                            <Form.Label>Enter Message</Form.Label>
                            <Form.Control rows="5" as="textarea" placeholder="Enter Message" />
                        </Form.Group>
                        <Form.Group controlId="formBasicCaptcha">
                            <HCaptcha sitekey="5346dd88-aeb9-4169-b438-9d39357af7b8" onVerify={this.onVerifyCaptcha}/>
                        </Form.Group>
                        <center><Button variant="primary" type="submit" style={{width:'75%', height:'40px' ,background:this.props.bgc}}>
                        Submit
                        </Button></center>
                    </Form>
                </Container>
            </Container>
            </LoadingOverlay>
        )
    }
}
export default DropALine;