import React from 'react';
import { Container, Col, Row, Button } from 'react-bootstrap';
import { ProgressBar } from 'react-step-progress-bar';
import "react-step-progress-bar/styles.css";
import skillconfig from '../skillconfig.json';

class Skills extends React.Component{
    render(){
        return(
            <Container fluid style={{backgroundColor:'#f9f9ff', paddingTop:'50px', paddingBottom:'50px', textAlign:'center'}} id={this.props.id}>
                <h1>My Skills</h1>
                <Container style={{paddingTop:'20px'}}>
                    <Row>
                        <Col xs={12} md={6}>
                            {Object.keys(skillconfig).map((key,i) => {
                                return(
                                    <p key={i} style={{padding:'5px'}}>
                                        <h5 key={i}>{key}</h5>
                                        <ProgressBar filledBackground={this.props.bgc} percent={skillconfig[key]}/>
                                    </p>
                                )
                            })}
                        </Col>
                        <Col xs={12} md={6}>
                            <Container style={{height:'100%', backgroundColor:'#ffffff'}}>
                                <div className="LI-profile-badge" style={{marginBottom:'20px'}} data-version="v1" data-size="large" data-locale="en_US" data-type="vertical" data-theme="light" data-vanity="pranavms13"><a class="LI-simple-link" href='https://in.linkedin.com/in/pranavms13?trk=profile-badge'>Pranav M S</a></div>
                                <p><Button style={{width:'250px' ,background:this.props.bgc}} onClick={() => {window.open('/resume/Profile.pdf','_blank')}}>View Resume</Button></p>
                                <p><Button style={{width:'250px' ,background:this.props.bgc}} onClick={() => {window.open('mailto:pranavms13@yahoo.com','_blank')}}>Hire Me</Button></p>
                            </Container>
                        </Col>
                    </Row>
                </Container>
            </Container>
        )
    }
}
export default Skills;