import React from 'react';
import { Card, Media,Container, Col, Row, Button } from 'react-bootstrap';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

import 'bootstrap/dist/css/bootstrap.min.css';

class GitterCard extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            userdetails: "",
            userimg : ""
        }
        this.username = this.props.username;
        this.follow = this.follow.bind(this);
    }
    componentWillMount(){
        fetch("https://api.github.com/users/"+this.props.username)
            .then(res => res.json())
            .then((result) => {
                fetch(result.avatar_url)
                    .then(res => res.blob())
                    .then((pic) => {
                        this.setState({userdetails: result ,userimg : window.URL.createObjectURL(pic)})
                    })
            });
    }
    follow(){
        window.open(this.state.userdetails.html_url,'_blank')
    }
    render(){
        return(
            <Card style={{ width: '100%', textAlign:'center', marginTop:'15px', boxShadow: '0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)'}}>
                <Media>
                <img width={120} height={120} className="mr-3" src={this.state.userimg} alt="Avatar"/>
                    <Media.Body>
                        <Container>
                            <div>
                                <FontAwesomeIcon icon={faGithub} size="2x" style={{margin:'5px'}}/>
                                <span style={{fontSize:'22px'}}><a href={this.state.userdetails.html_url} style={{color: '#000000'}}>
                                    {this.state.userdetails.name}{' '}
                                    <span style={{fontSize:'12px'}}>
                                        {this.state.userdetails.login}
                                    </span></a>
                                </span>
                            </div>
                            <Button variant="success" onClick={this.follow} style={{width:'100%', padding:'2px', margin:'2px', background:this.props.bgc}}>Follow</Button>
                            <Row style={{paddingTop:'5px'}}>
                                <Col xs={4}>
                                    <small><b>Repos :</b> {this.state.userdetails.public_repos}</small>
                                </Col>
                                <Col xs={4}>
                                    <small><b>Followers :</b> {this.state.userdetails.followers}</small>
                                </Col>
                                <Col xs={4}>
                                    <small><b>Following :</b> {this.state.userdetails.following}</small>
                                </Col>
                            </Row>
                        </Container>
                    </Media.Body>
                </Media>
            </Card>
        )
    }
}
export default GitterCard;